<template>
<a href="link || 'javascript:;'" :link="routerLink" class="weui-tabbar__item" :class="{'weui-bar__item-on': isOn}">
  <div class="weui-tabbar__icon">
    <slot name="icon"></slot>
  </div>
  <div class="weui-tabbar__label">
    <slot name="label"></slot>
  </div>
</a>
</template>

<script>
export default {
  props: {
    /**
     * 
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     */
    routerLink: {
      type: null,
      required: false
    },

    /**
     * 
     */
    isOn: {
      type: Boolean,
      required: false,
      default: false
    }
  }
}
</script>



