<template>
<li :class="{'weui-uploader__file': true, 'weui-uploader__file_status': hasStatus}" :style="{'backgroundImage': 'url(' + imageUrl + ')'}">
  <div class="weui-uploader__file-content">
    <slot name="status"></slot>
  </div>
</li>
</template>

<script>
export default {
  props: {
    /**
     * urlbackgroud-image
     */
    imageUrl: {
      type: String,
      required: true
    },

    /**
     * status
     * 
     */
    hasStatus: {
      type: Boolean,
      required: false,
      default: false
    }
  }
}
</script>



