<template>
<a :href="link || 'javascript:;'" :link="routerLink" class="weui-media-box" :class="typeClass" v-if="!!link || !!routerLink">
  <slot></slot>
</a>
<div class="weui-media-box" :class="typeClass" v-else>
  <slot></slot>
</div>
</template>

<script>
export default {
  props: {
    /**
     * 
     * appmsg
     * text
     * small-appmsg
     */
    type: {
      type: String,
      required: true
    },

    /**
     * 
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     */
    routerLink: {
      type: null,
      required: false
    }
  },

  computed: {
    typeClass() {
      return `weui-media-box_${this.type}`;
    }
  }
}
</script>



