<template>
<a :href="link" :link="routerLink" class="weui-grid">
  <div class="weui-grid__icon" v-if="imageUrl">
    <img :src="imageUrl" alt="">
  </div>
  <p class="weui-grid__label" v-if="label">{{label}}</p>
</a>
</template>

<script>
export default {
  props: {
    /**
     * href
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     * v-link
     */
    routerLink: {
      type: null,
      required: false
    },

    /**
     * 
     */
    imageUrl: {
      type: String,
      required: false
    },

    /**
     * 
     */
    label: {
      type: null,
      required: false
    }
  }
}
</script>



