<template>
<a href="javascript:;" class="weui-btn" :class="[typeClass, disabled ? 'weui-btn_disabled' : '', mini ? 'weui-btn_mini' : '']">
  <slot></slot>
</a>
</template>

<script>
export default {
  props: {
    /**
     * 
     * primary: 
     * warn
     * default
     */
    type: {
      type: String,
      default: 'primary',
      required: false
    },

    /**
     * disabledtrue
     */
    disabled: {
      type: Boolean,
      default: false,
      required: false
    },

    /**
     * 
     */
    mini: {
      type: Boolean,
      default: false,
      required: false
    },

    /**
     * plainplain
     * primarydefaultplain
     */
    plain: {
      type: Boolean,
      default: false,
      required: false
    }
  },

  computed: {
    typeClass() {
      return `weui-btn${this.plain ? '_plain' : ''}_${this.type}`;
    }
  }
}
</script>



