<template>
<i :class="[nameClass, typeClass]"></i>
</template>

<script>
export default {
  props: {
    /**
     * 
     * success, success_circle, success_no_circle,
     * info, waiting, waiting_circle, circle,
     * warn, download, info_circle, cancel
     */
    name: {
      type: String,
      required: true
    },

    /**
     * 
     * default: 
     * message: 
     * safe: 
     */
    type: {
      type: String,
      required: false,
      default: 'default'
    }
  },

  computed: {
    nameClass() {
      return `weui-icon${this.type === 'safe' ? '-safe' : ''}-${this.name}`
    },

    typeClass() {
      switch(this.type) {
        case 'message':
          return 'weui-icon_msg';
        case 'safe':
          return 'weui-icon-safe';
        default:
          return false;
      }
    }
  }
}
</script>



