<template>
<div class="weui-panel" :class="{'weui-panel_access': access}">
  <slot></slot>
</div>
</template>

<script>
export default {
  props: {
    /**
     * 
     */
    access: {
      type: Boolean,
      required: false,
      default: false
    }
  }
}
</script>



