<template>
<img :src="src" :alt="alt" class="weui-media-box__thumb">
</template>

<script>
export default {
  props: {
    /**
     * src
     */
    src: {
      type: String,
      required: true
    },

    /**
     * alt
     */
    alt: {
      type: String,
      required: false
    }
  }
}
</script>



