<template>
<a class="weui-panel__ft" :href="link || 'javascript:;'" :link="routerLink"><slot></slot></a>
</template>

<script>
export default {
  props: {
    /**
     * 
     */
    link: {
      type: String,
      required: false
    },

    /**
     * vue-router
     * vue-routerrouter-linklink
     */
    routerLink: {
      type: null,
      required: false
    }
  }
}
</script>



